#gradient-bg
{
  position: fixed;
  z-index: -100;
  
  top: 0px;
  left: 0px;
  
  height: 100vh;
  width: 100vw;
  
  background: rgb(84,16,221);
  background: linear-gradient(0deg, rgba(84,16,221,1) 0%, rgba(149,0,187,1) 100%);
  background-attachment: fixed !important;
}

.switch-wrapper
{
  position: relative;
}

.switch-wrapper > div {
  
  position: absolute;
}
