@keyframes splashButton
{
  0% 
  {
    top: -5px;
  }
  
  50%
  {
    top: 5px;
  }
  
  100%
  {
    top: -5px;
  }
}

.floating
{
  animation-name: splashButton;
  animation-duration: 2s;
  animation-timing-function: ease-in-out;
  animation-iteration-count: infinite;
}

.splash-button-text
{
  font-size: 1em;
  
  color: rgba(255, 255, 255, 0.69);
  transform: scale(1);
  
  transition-property: all;
  transition-duration: 0.75s;
}

.splash-button-text:hover
{
  color: rgba(255, 255, 255, 1);
  transform: scale(1.1);
}

.selection-button
{
  color: rgba(255, 255, 255, 1);
  border-color: rgba(255, 255, 255, 1);
  
  font-size: 1.5em;
  width: 350px;
  
  transition-property: all;
  transition-duration: 0.3s;
}

.selection-button:hover
{
  color: rgba(255, 255, 0, 1);
  border-color: rgba(255, 255, 0, 1);
  
  font-size: 2em;
  width: 466px;
}

.cell
{
  display: flex;
  flex-direction: column;
  
  justify-content: center;
  align-items: center;
  
  border-radius: 10px;
  
  text-decoration: none;
  text-align: center;
}

.cell-category
{
  background: rgba(15, 19, 125, 0.85);
  
  font-weight: bold;
  letter-spacing: 1px;
  
  color: rgba(255, 255, 255, 1);
}

.cell-card
{
  background: rgba(43, 38, 181, 0.85);
  color: rgba(255, 255, 255, 1);
  
  font-weight: bold;
  font-size: 1.25em;
  
  transition-property: all;
  transition-duration: 0.2s;
}

.cell-card:hover
{
  background: rgba(43, 38, 181, 1);
  color: rgba(255, 255, 128, 1);
  font-size: 1.5em;
}

.cell-card-completed
{
  background: rgba(43, 38, 181, 0.5);
  color: rgba(255, 255, 255, 0.25);
  
  font-weight: bold;
  font-size: 1.25em;
  
  cursor: default;
}

.reveal-label
{
  color: rgba(255, 255, 255, 0.5);
  
  transition-property: all;
  transition-duration: 0.25s;
}

.reveal-label:hover
{
  color: rgba(255, 255, 255, 1);
}

.reveal-label-double
{
  color: rgba(0, 0, 0, 0.5);
  
  transition-property: all;
  transition-duration: 0.25s;
}

.reveal-label-double:hover
{
  color: rgba(0, 0, 0, 1);
}

.card-question > p
{
  font-size: 1.75em;
}